import React from 'react';

export default class Contact extends React.Component {
    constructor(props) {
        super(props);

        this.header = "Contact information"
        this.email = "For my email look above!"
        this.github = "https://www.github.com/ilyayudkovich"
        this.linkedin = "https://www.linkedin.com/in/ilya-yudkovich-10aa60b1/"
        this.state = {
            open: false,
            toggleShow: this.props.toggleShow
        }
    }

    handleClick  = () => {
        this.props.toggleShow(true, this.header, this.getHtmlContent());
    }

    getHtmlContent() {
        return(
            <div className="return-value text-left">
                <p><b>Email: </b>Look Above!</p>
                <p><b>GitHub: </b><a href={this.github}>{this.github}</a></p>
                <p><b>LinkedIn: </b><a href={this.linkedin}>{this.linkedin}</a></p>
                <p className="text-center">Feel free to contact me anytime</p>
            </div>
        )
    }

    render() {
        return(
            <div className="content">
                <div className="col">
                <button className="btn btn-block" type="button" onClick={this.handleClick}>
                    <strong>Contact</strong>
                </button>
                </div>
            </div>
        )}
}